/**=====================
     logo section scss
==========================**/
.logo-marketing {
    .logo-slider {
        filter: drop-shadow(0px 24px 30px rgba(var(--theme-color2), 0.07));
        position: relative;
        z-index: 1;

        &::after {
            content: "";
            position: absolute;
            left: -33px;
            top: -7px;
            width: 100%;
            height: 110%;
            background-image: url("../svg/shapes/rectangle.svg");
            background-repeat: no-repeat;
            z-index: -1;
            background-size: contain;

            @media (max-width: 1199px) {
                top: 1px;
            }

            @media (max-width: 991px) {
                display: none;
            }
        }

        .clipath-wrap {
            padding: calc(35px + (65 - 35) * ((100vw - 320px) / (1920 - 320))) 20px;
            clip-path: polygon(0 5%, 100% 0, 100% 100%, 0 97%);
            background-color: rgba(var(--white), 1);

            .logo-img {
                text-align: center;
                filter: brightness(0);
                transform: scale(0.9);
                opacity: 0.2;
                transition: all 0.5s ease;

                &:hover {
                    filter: brightness(1);
                    opacity: 1;
                    transform: scale(1);
                    transition: all 0.5s ease;
                }
            }
        }
    }

    .swiper-wrapper {
        transition-timing-function: linear;
    }

    &.logo-business {
        .title-style-6 {
            h2 {
                width: 60%;

                @media (max-width: 767px) {
                    width: 75%;
                }

                @media (max-width: 576px) {
                    width: 100%;
                }
            }
        }

        .logo-slider {
            &::after {
                display: none;
            }
        }
    }
}

// logo app
.logo-app {
    .logo-img {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: calc(15px + (25 - 15) * ((100vw - 320px) / (1920 - 320))) calc(30px + (60 - 30) * ((100vw - 320px) / (1920 - 320)));
        background-color: rgba(var(--white), 1);
        border: 1px solid rgba(var(--title-color), 0.1);
        height: calc(52px + (82 - 52) * ((100vw - 320px) / (1920 - 320)));
        border-radius: 5px;
        filter: grayscale(1);
        transition: all 0.5s ease;

        img {
            opacity: 0.5;
            transition: all 0.5s ease;
        }

        &:hover {
            filter: grayscale(0);
            border: 1px solid rgba(var(--title-color), 0.5);

            img {
                opacity: 1;
            }
        }
    }
}

// logo ecommerce
.logo-ecommerce {
    padding: calc(30px + (50 - 30) * ((100vw - 320px) / (1920 - 320))) 0;

    .logo-img {
        background-color: rgba(var(--white), 0.5);
        height: calc(56px + (96 - 56) * ((100vw - 320px) / (1920 - 320)));
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;

        img {
            width: 80%;
            transition: all 0.5s ease;
        }

        &:hover {
            img {
                filter: grayscale(1);
                transform: scale(1.1);
            }
        }
    }

    &.logo-podcast {
        clip-path: polygon(0 13%, 100% 0, 100% 100%, 0% 100%);

        .logo-img {
            background: radial-gradient(85.71% 85.71% at 50% 50%,
                    rgba(var(--white), 0) 0%,
                    rgba(var(--white), 0.4) 100%);
            backdrop-filter: blur(2px);
            border-radius: 10px;

            img {
                width: 90%;
            }
        }
    }
}

// logo seo
.seo-logo-section {
    background: linear-gradient(188deg, rgba(81, 147, 255, 1) 0%, rgba(36, 119, 255, 1) 100%);
    text-align: center;

    .custom-g {
        --bs-gutter-x: 2px;
        --bs-gutter-y: 2px;
    }

    .logo-title {
        h2 {
            color: rgba(var(--white), 1);
            margin-bottom: 30px;

            span {
                color: #FFB600;
            }
        }
    }

    .logo-box {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: calc(18px + (28 - 18) * ((100vw - 320px) / (1920 - 320)));
        position: relative;
        overflow: hidden;

        &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: radial-gradient(circle, rgba(var(--white), 0.2) 0%, rgba(var(--white), 0) 50%, rgba(var(--white), 0.2) 100%);
            transform: scale(4);
            transform-origin: top;
        }
    }
}