/*=====================
    Features Scss start
==========================*/
.marketing-feature {
    padding: calc(20px + (40 - 20) * ((100vw - 320px) / (1920 - 320)));
    background: linear-gradient(270deg, rgba(var(--theme-color2), 1) 23.43%, rgba(var(--theme-color2), 0.95) 50%);
    border-radius: 20px;
    position: relative;
    overflow: hidden;

    .core-effect1 {
        position: absolute;
        left: 0;
        top: 0;

        img {
            opacity: 0.2;
        }
    }

    .core-effect2 {
        position: absolute;
        right: 0;
        bottom: 0;

        img {
            opacity: 0.3;
        }
    }

    .core-effect3 {
        position: absolute;
        right: -24%;
        bottom: -25%;

        img {
            opacity: 0.9;
        }

        @media (max-width: 1400px) {
            right: -34%;
        }
    }

    .core-effect4 {
        position: absolute;
        top: 50%;
        left: 32%;

        @media (max-width: 1400px) {
            top: 30%;
            left: 82%;
        }

        img {
            opacity: 0.7;
            animation: mymove 5s infinite;
        }
    }

    .title-style-1 {
        h5 {
            &:after {
                background-color: rgba(var(--white), 0.1);
            }
        }
    }

    .feature-counter {
        margin-top: 45px;
        display: flex;
        align-items: center;
        gap: 30px;

        @media (max-width: 991px) {
            margin-top: 15px;
        }

        h2 {
            font-size: 50px;

            @media (max-width: 991px) {
                font-size: 36px;
            }
        }

        h4 {
            color: rgba(var(--white), 1);
        }

        li {
            &:first-child {
                h2 {
                    color: #a9deb4;
                }
            }

            &:nth-child(2) {
                h2 {
                    color: #ffd100;
                }
            }
        }
    }

    .feature-box {

        h3,
        p {
            color: rgba(var(--white), 1);
        }

        h3 {
            font-weight: 700;
            margin-bottom: calc(6px + (10 - 6) * ((100vw - 320px) / (1920 - 320)));
            font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
        }

        p {
            margin-bottom: 0;
            font-weight: 500;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            font-size: calc(13px + (16 - 13) * ((100vw - 320px) / (1920 - 320)));
        }

        .feature-icon {
            width: 60px;
            height: 60px;
            background-color: rgba(var(--white), 0.1);
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 100%;
            margin-bottom: 12px;
            position: relative;

            &:after {
                content: "";
                position: absolute;
                background-image: url(../svg/shapes/lines.svg);
                background-size: contain;
                width: 100%;
                height: 100%;
            }
        }
    }
}

.app-features-section {
    .title-style-2 {
        h2 {
            width: 50%;
            margin: 0 auto;

            @media (max-width: 1199px) {
                width: 62%;
            }

            @media (max-width: 767px) {
                width: 85%;
            }

            @media (max-width: 576px) {
                width: 100%;
            }
        }
    }

    .middle-phone {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        .blob-img {
            position: absolute;
            left: 0;
            top: 0;
            z-index: -1;
            transform: rotate(-180deg);
            animation: transform 30s ease-in-out infinite both alternate, 60s ease-in-out infinite both;
        }
    }

    .features-listing {
        display: flex;
        gap: 55px;
        flex-wrap: wrap;

        @media (max-width: 991px) {
            gap: 35px;
        }

        @media (max-width: 767px) {
            gap: 25px;
        }

        li {
            display: flex;
            align-items: flex-start;
            gap: 20px;

            @media (max-width: 1199px) {
                font-size: 14px;
            }

            h3 {
                color: rgba(var(--title-color), 1);
                font-weight: 500;
                margin-bottom: calc(5px + (12 - 5) * ((100vw - 320px) / (1920 - 320)));
                font-family: $space-grotesk;

                @media (max-width: 1199px) {
                    font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
                }
            }

            p {
                font-size: 18px;
                color: rgba(var(--content-color), 1);
                font-weight: 500;
                margin-bottom: 0;

                @media (max-width: 1199px) {
                    font-size: 14px;
                }
            }

            img {
                width: calc(35px + (45 - 35) * ((100vw - 320px) / (1920 - 320)));
                height: calc(35px + (45 - 35) * ((100vw - 320px) / (1920 - 320)));
                object-fit: contain;
            }
        }
    }

    .row {
        @media (max-width: 767px) {
            >div {
                &:first-child {
                    margin-bottom: 25px;
                }
            }
        }
    }
}

.business-feature {
    .title-style-6 {
        width: 74%;
        margin-inline: auto;

        @media (max-width: 576px) {
            width: 100%;
        }
    }

    .custom-gap {
        --bs-gutter-x: 100px;
        --bs-gutter-y: 100px;

        @media (max-width: 1430px) {
            --bs-gutter-x: 80px;
            --bs-gutter-y: 80px;
        }

        @media (max-width: 1199px) {
            --bs-gutter-x: 60px;
            --bs-gutter-y: 60px;
        }

        @media (max-width: 991px) {
            --bs-gutter-y: 40px;
        }

        @media (max-width: 576px) {
            --bs-gutter-x: 15px;
        }

        >div {
            position: relative;

            @media (max-width: 991px) {
                &:nth-child(3) {
                    &::after {
                        display: none;
                    }
                }
            }

            +div {
                &::after {
                    content: "";
                    background: radial-gradient(circle, hsla(206, 40%, 42%, 1) 0%, hsla(207, 41%, 22%, 0) 100%);
                    width: 1px;
                    height: 100%;
                    position: absolute;
                    left: 0;
                    top: 0;
                }
            }
        }
    }

    .feature-box {
        .feature-icon {
            width: 54px;
            height: 54px;
            border-radius: 10px;
            background-color: #192E3E;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 15px;

            @media (max-width: 991px) {
                width: 44px;
                height: 44px;
                padding: 10px;
            }
        }

        h3 {
            font-size: 26px;
            font-weight: bold;
            color: rgba(var(--white), 1);
            position: relative;
            display: inline-block;
            padding-right: 20px;
            margin-bottom: 13px;

            @media (max-width: 1430px) {
                font-size: 20px;
            }

            @media (max-width: 1199px) {
                font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
                padding-right: 0;
            }

            &::after {
                content: "";
                height: 12px;
                background-color: rgba(var(--white), 0.10);
                width: 100%;
                position: absolute;
                left: -10px;
                bottom: -1px;

                @media (max-width: 576px) {
                    left: 0;
                }
            }
        }

        p {
            color: rgba(var(--white), 0.7);
            margin-bottom: -6px;
            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
        }
    }
}

.saas-feature-section {
    .feature-box {
        box-shadow: 0 14px 40px 0 rgba(#181B4B, 0.06);
        background-color: rgba(var(--white), 1);
        padding: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320))) calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320)));
        border-radius: 10px;
        transition: all 0.5s ease;
        height: 100%;
        position: relative;
        z-index: 1;

        &::after {
            content: "";
            position: absolute;
            height: 35px;
            width: calc(100% - 20px);
            background-color: rgba(var(--white), 0.04);
            border-radius: 20px;
            transform: rotate(-35deg) translateY(-50%);
            top: 50%;
            z-index: -1;
            transition: all 0.5s ease;
        }

        .feature-icon {
            width: calc(48px + (58 - 48) * ((100vw - 320px) / (1920 - 320)));
            height: calc(48px + (58 - 48) * ((100vw - 320px) / (1920 - 320)));
            border-radius: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: calc(12px + (20 - 12) * ((100vw - 320px) / (1920 - 320)));
            background-color: rgba(var(--theme-color1), 0.10);
            transition: all 0.5s ease;
            padding: 12px;

            &.color1 {
                background-color: #FFF8EC;
            }

            &.color2 {
                background-color: #E8F6EA;
            }

            &.color3 {
                background-color: #FFF2F3;
            }

            &.color4 {
                background-color: #E5F7FD;
            }
        }

        h4 {
            color: rgba(var(--title-color), 1);
            font-weight: 700;
            margin-bottom: calc(4px + (8 - 4) * ((100vw - 320px) / (1920 - 320)));
            line-height: 1.5;
        }

        p {
            color: rgba(var(--content-color), 1);
            margin-bottom: 0;
            font-weight: 500;
        }

        &:hover {
            background: rgb(132, 121, 255);
            background: linear-gradient(90deg, rgba(132, 121, 255, 1) 0%, rgba(94, 80, 255, 1) 100%);
            box-shadow: none;
            transition: all 0.5s ease;

            &:after {
                visibility: visible;
                opacity: 1;
                transition: all 0.5s ease;
            }

            h4,
            p {
                color: rgba(var(--white), 1);
            }

            .feature-icon {
                background-color: rgba(var(--white), 1);
                transition: all 0.5s ease;
            }
        }
    }
}

.seo-feature-section {
    position: relative;

    .title-style-7 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
    }

    .feature-effect {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);

        @media (max-width: 1700px) {
            left: -7%;
        }

        @media (max-width: 1400px) {
            display: none;
        }
    }

    .feature-box {
        filter: drop-shadow(0 24px 30px rgba(var(--theme-color1), 0.06));
        position: relative;

        &:after {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: calc(100% - 50px);
            background-color: rgba(var(--theme-color1), 1);
            border-radius: 5px;
            z-index: -1;
            transform: scale(0.9);
            transition: all 0.5s ease;
        }

        &:hover {
            &:after {
                transform: scale(1);
                left: 12px;
                bottom: -7px;
                box-shadow: 10px 16px 20px 0 rgba(var(--theme-color1), 0.10);
            }
        }

        >div {
            padding: calc(30px + (40 - 30) * ((100vw - 320px) / (1920 - 320))) 20px calc(40px + (60 - 40) * ((100vw - 320px) / (1920 - 320)));
            clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 96%);
            background: rgba(var(--white), 1);
        }

        .feature-icon {
            width: 60px;
            height: 60px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            margin-bottom: calc(14px + (25 - 14) * ((100vw - 320px) / (1920 - 320)));

            .effect-bg {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                z-index: -1;
            }
        }

        h3 {
            font-size: calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320)));
            color: rgba(var(--title-color), 1);
            font-weight: 500;
            position: relative;
            margin-bottom: calc(8px + (15 - 8) * ((100vw - 320px) / (1920 - 320)));

            &:after {
                content: "";
                width: 74px;
                height: 11px;
                position: absolute;
                left: 0;
                bottom: 1px;
                background-color: rgba(var(--theme-color1), 0.1);
            }
        }

        p {
            font-size: 16px;
            margin-bottom: 0;
            color: rgba(var(--content-color), 1);
        }
    }
}