/**=====================
     header scss
==========================**/

header {
    padding-top: calc(15px + (30 - 15) * ((100vw - 320px) / (1920 - 320)));
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 2;
    transition: all 0.5s ease;

    .header-logo {
        @media (max-width: 576px) {
            img {
                width: 76%;
            }
        }
    }

    .container,
    .custom-container,
    .custom-lg-container,
    .container-fluid {
        display: flex;
        align-items: center;
    }

    .bar-option {
        background: rgba(var(--white), 0.3);
        box-shadow: 0px 14px 20px rgba(var(--theme-color1), 0.04);
        backdrop-filter: blur(2px);
        border-radius: 5px;
        padding: calc(6px + (10 - 6) * ((100vw - 320px) / (1920 - 320))) calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
        transition: all 0.5s ease;
        display: flex;
        align-items: center;

        i {
            color: rgba(var(--black), 1);
            font-weight: 600;
        }

        &:hover {
            transition: all 0.5s ease;
            transform: translateY(-5px);
            box-shadow: 0px 7px 20px rgba(var(--theme-color1), 0.08);
        }

        &.btn-color1 {
            background-color: rgba(var(--theme-color1), 1);
            color: rgba(var(--white), 1);

            i {
                color: rgba(var(--white), 1);
            }
        }

        &.btn-color2 {
            background-color: rgba(var(--theme-color2), 1);
            color: rgba(var(--white), 1);

            i {
                color: rgba(var(--white), 1);
            }
        }
    }

    &.light-header {
        .navbar {
            &.navbar-expand-xl {
                .navbar-nav {
                    .nav-link {
                        color: rgba(var(--white), 1);

                        &:hover {
                            color: rgba(var(--white), 1);
                        }
                    }
                }
            }
        }
    }
}

body {
    &:after {
        content: "";
        position: fixed;
        left: 0;
        top: 0;
        height: 0;
        width: 100%;
        background-color: rgba(var(--white), 1);
        transition: height 0.5s ease-out;
        box-shadow: 0px 8px 20px 0px rgba(var(--theme-color1), 0.04);
        z-index: 1;
    }

    &.sticky-header {
        &:after {
            height: 77px;

            @media (max-width: 767px) {
                height: 70px;
            }
        }

        header {
            position: fixed;
            left: 0;
            top: 0;
            z-index: 2;
            padding-top: 15px;
            padding-bottom: 15px;

            .header-logo,
            .btn,
            .bar-option {
                z-index: 1;
            }

            &.light-header {
                .navbar {
                    &.navbar-expand-xl {
                        .navbar-nav {
                            .nav-link {
                                color: rgba(var(--title-color), 1);

                                &:hover {
                                    color: rgba(var(--title-color), 1);
                                }
                            }
                        }
                    }
                }

                .bar-option {
                    background-color: rgba(var(--theme-color1), 1) !important;

                    i {
                        color: rgba(var(--white), 1) !important;

                    }
                }

                .btn-outline-light {
                    border-color: rgba(var(--theme-color1), 1) !important;
                    color: rgba(var(--theme-color1), 1) !important;
                }
            }
        }

    }
}