/*=====================
    App Scss start
==========================*/
// app showcase
.app-section {
    position: relative;
    padding-top: 70px;
    @media (max-width: 1400px) {
        padding-top: 65px;
    }
    @media (max-width: 1060px) {
        padding-top: 35px;
    }
    .phone {
        position: absolute;
        bottom: 0;
        z-index: 1;
        right: 16%;
        @media (max-width: 1600px) {
            right: 10%;
        }
        @media (max-width: 1199px) {
            width: 30%;
        }
    }
    .section-space {
        padding: 100px 60px;
        position: relative;
        z-index: 0;
        overflow: hidden;
        @media (max-width: 1199px) {
            padding: 40px 60px;
        }
        @media (max-width: 767px) {
            padding: calc(30px + (40 - 30) * ((100vw - 320px) / (1920 - 320)))
                calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
        }
    }
    .app-buttons {
        display: flex;
        align-items: center;
        gap: calc(8px + (16 - 8) * ((100vw - 320px) / (1920 - 320)));
        margin-top: calc(14px + (30 - 14) * ((100vw - 320px) / (1920 - 320)));
        @media (max-width: 1199px) {
            width: 80%;
        }
    }
    .app-images {
        .blob-img {
            position: absolute;
            right: 11%;
            top: 13px;
            z-index: -1;
            animation: myroate 100s infinite;
            @media (max-width: 1199px) {
                width: 40%;
                right: 8%;
                top: 20%;
            }
        }
    }
}

// fitness
.fitness-app-sec {
    .app-content {
        display: flex;
        align-items: center;
        height: 100%;
    }
    .app-listing {
        li {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            h3 {
                font-weight: 500;
                color: rgba(var(--title-color), 1);
            }
            h4 {
                font-weight: 500;
                color: rgba(var(--content-color), 1);
            }
            + li {
                margin-top: 30px;
            }
        }
    }
    .store-btn {
        display: flex;
        align-items: center;
        gap: 18px;
        margin-top: 30px;
    }
    .app-img {
        display: flex;
        justify-content: flex-end;
        position: relative;
        margin-bottom: -25px;
        .girl-img {
            position: absolute;
            left: -21%;
            bottom: 60px;
        }
        .shape-img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: -1;
        }
        &:after {
            content: "";
            position: absolute;
            border-radius: 70% 30% 30% 70% / 60% 40% 60% 40%;
            width: 82%;
            height: 82%;
            background: rgba(var(--theme-color1), 1);
            top: 20px;
            right: 0;
            transform: rotate(-180deg);
            animation: 30s ease-in-out 0s infinite alternate both running transform,
                60s ease-in-out 0s infinite normal both running;
            z-index: -1;
        }
    }
}
