/*=====================
    form Scss start
==========================*/
input[type="number"] {
    &::-webkit-inner-spin-button {
        display: none;
    }
}

.input-group {

    >.form-control,
    >.form-floating,
    >.form-select {

        &:focus,
        &:focus-within {
            box-shadow: none;
            z-index: unset;
        }
    }
}

.form-style-1 {
    .form-control {
        border-radius: 5px;
        border: none;
        background-color: rgba(var(--white), 0.15);
        font-size: 16px;
        color: rgba(var(--white), 1);
        font-weight: 500;
        padding: 13px;
        position: relative;

        &::placeholder {
            color: rgba(var(--white), 0.7);
        }

        &:focus {
            box-shadow: none;
        }
    }

    .form-group {
        position: relative;

        .action-btn {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 14px;
            color: rgba(var(--theme-color3), 1);
            font-weight: 500;
        }
    }
}

.form-style-2 {
    .form-group {
        position: relative;

        i {
            position: absolute;
            right: 0;
            color: rgba(var(--title-color), 1);
            top: 15px;
        }
    }

    .form-control {
        border-radius: 5px;
        border: none;
        border-bottom: 1px solid rgba(var(--title-color), 0.5);
        background-color: transparent;
        font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
        color: rgba(var(--title-color), 1);
        font-weight: 500;
        padding: calc(8px + (13 - 8) * ((100vw - 320px) / (1920 - 320))) 0;
        position: relative;
        border-radius: 0;

        &::placeholder {
            text-transform: capitalize;
            color: rgba(var(--title-color), 0.7);
        }

        &:focus {
            box-shadow: none;
        }
    }

    &.light-version {
        .form-group {
            i {
                color: rgba(var(--white), 1);
            }
        }

        .form-control {
            border-bottom: 1px solid rgba(var(--white), 0.5);
            color: rgba(var(--white), 1);

            &::placeholder {
                color: rgba(var(--white), 0.7);
            }
        }
    }
}